<template>
    <!-- 列表渲染 -->
    <div class="post">
        <div class="row">
            <div id="posts" class="col-md-8 col-md-offset-2">
                <!-- Category/Tag -->
                <div class="header" v-if="categoryData!==null">
                    <div
                        class="tl-leader"
                    >
                        <h1>
                            <span class="divider">/</span>
                            <span>{{categoryData.name}}</span>
                        </h1>
                    </div>
                </div>
                <!-- 循环 -->
                <div class="post-wrapper">
                    <!-- <PostCard
                        v-for="post in postTocList"
                        :key="post.title"
                        :post="post"
                        :timeMode='page>2?0:1'
                        :top = true
                    >
                    </PostCard> -->
                    <PostCard
                        v-for="(post,index) in postList"
                        :key="index"
                        :post="post"
                        :timeMode='page>2?0:1'
                    >
                    </PostCard>
                </div>
                <!--分页-->
                <div class="tl-pagination"  v-if="postList.length>0">
                    <div class="prev" v-if="previousUrl!==null">
                        <div class="item" @click="handlePage('prev')">
                            <div class="num">
                                {{ page-1 }}
                            </div>
                            <div class="title">上一页</div>
                         </div>
                    </div>
                    <div class="next" v-if="nextUrl!==null">
                        <div class="item" @click="handlePage('next')" >
                            <div class="num">
                                {{ page }}
                            </div>
                            <div class="title">下一页</div>
                         </div>
                    </div>
                </div> 
             </div> 
             
        </div>
    </div>
</template>

<script>
import {
    getAll,
    getCategory,
    postTop
} from '../../../../api/blog/index'
import PostCard from './components/PostCard';
import gsap from 'gsap';

import { ContentLoader, FacebookLoader, CodeLoader, BulletListLoader } from 'vue-content-loader'




export default {
    components: {
        PostCard,
        ContentLoader,
        CodeLoader,
        BulletListLoader
    },
    data(){
        return {
            page: 1,
            size: 8,
            categoryID: null,
            
            postList: [], // 文章列表
            postTocList: [], // Mark的文章

            categoryData: null, // 分类信息
            nextUrl: '', // 下一页
            previousUrl: '',  // 上一页
        }
    },
    methods: {
        
        // 加载推荐文章
        loadPostTop(){
            postTop().then(res=>{
                this.postTocList = res.data;
            })
        },
        // 加载动画
        loadAnime(){
            gsap.from('.post-wrapper', {
                        duration: 0.6,
                        y: '10px',
                        opacity: 0,
                        ease: 'Power1.easeInOut'
                })
        },
        // 加载文章
        loadPost(){
            let fm = {};
            fm.page = this.page;
            fm.size = this.size;
            fm.type = 1;
            if(this.categoryID !== null){
                fm.category = this.categoryID;
            }

            getAll(fm).then(res=>{
                this.nextUrl = res.next;
                this.previousUrl = res.previous;
                this.postList = [
                    ...res.results,
                    ...this.postTocList,]
                this.loadAnime();
            })
        },
        loadCategory(id){
            getCategory(id).then(res=>{
                this.categoryData = res;
            })
        },
        handlePage(type){
            const that = this;
            let fm =  {};
            if(type==='next')
                fm.page = parseInt(this.page)+1
            else if(type ==='prev')
                fm.page = parseInt(this.page)-1
            if(this.categoryID !== null){
                fm.category = this.categoryID;
            }
            this.$router.push({
                path: '/blog/',
                query: fm
            });
        }
    },
    mounted(){
        this.categoryID= this.$route.query.category;
        this.page = this.$route.query.page || 1;
        // 加载
        this.loadPost();
    }
}
</script>

